/* HEADER HERO */
.gheader__hero {
  position: relative;
  max-width: calc(40% - 40px);
  min-width: calc(40% - 40px);
  padding: 0 40px 0 0;
}
.gworkspace__container {
  position: relative;
}
.video-play-button {
  position: absolute;
  z-index: 10;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  box-sizing: content-box;
  display: block;
  width: 32px;
  height: 44px;
  /* background: #fa183d; */
  border-radius: 50%;
  padding: 18px 20px 18px 28px;
}
.video-play-button:before {
  content: "";
  position: absolute;
  z-index: 0;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
  display: block;
  width: 55px;
  height: 55px;
  background: #ba1f24;
  border-radius: 50%;
  animation: pulse-border 1500ms ease-out infinite;
}
.video-play-button:after {
  content: "";
  position: absolute;
  z-index: 1;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
  display: block;
  width: 55px;
  height: 55px;
  background: #fa183d;
  border-radius: 50%;
  transition: all 200ms;
}
.video-play-button:hover:after {
  background-color: darken(#fa183d, 10%);
}
.video-play-button span {
  position: absolute;
  color: #fff;
  top: 50%;
  left: 52%;
  transform: translate(-50%, -50%);
  z-index: 10;
  font-size: 20px;
}
@keyframes pulse-border {
  0% {
    transform: translateX(-50%) translateY(-50%) translateZ(0) scale(1);
    opacity: 1;
  }
  100% {
    transform: translateX(-50%) translateY(-50%) translateZ(0) scale(1.5);
    opacity: 0;
  }
}
.circle__grid__top {
  position: absolute;
  top: -65px;
  left: -10px;
  width: 250px;
  z-index: -1;
}
.circle__grid__bottom {
  position: absolute;
  bottom: -60px;
  right: -60px;
  width: 250px;
  z-index: -1;
}
