/* BARANGAY HEADER SECTION */
#barangayHeaderSection {
  position: relative;
  width: 100%;
  height: auto;
  padding: 300px 0 50px;
}

/* BARANGAY HEADER CONTENT */
.barangay__header_content {
  position: relative;
  max-width: calc(50% - 80px);
  min-width: calc(50% - 80px);
  padding: 0 40px;
}
.barangay__header__subtagline {
  letter-spacing: 1px;
  font-weight: 400;
}
.barangay__header__tagline {
  letter-spacing: 1px;
  margin-bottom: 30px;
  font-weight: 400;
  display: inline-block;
  background: linear-gradient(90deg, #3680f7, #f0ad47);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
/* BARANGAY HEADER CONTENT */

/* BARANGAY HEADER HERO */
.barangay__header__hero {
  position: relative;
  max-width: calc(50% - 0px);
  min-width: calc(50% - 0px);
  padding: 0;
}
/* BARANGAY HEADER HERO */
/* BARANGAY HEADER SECTION */

/* BARANGAY SERVICES PROMO SECTION */
#barangayServicesPromoSection {
  position: relative;
  width: 100%;
  height: auto;
  padding-bottom: 10rem;
}

/* BARANGAY SERVICES PROMO THUMBNAIL */
.barangay__services__promo__container {
  position: relative;
  width: 100%;
  padding: 0 40px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}
.barangay__services__promo__thumbnail {
  position: relative;
  max-width: calc(50% - 92px);
  min-width: calc(50% - 92px);
  padding: 30px 20px 30px 40px;
  background: hsla(0, 0%, 100%, 0.3);
  /* box-shadow: 0 8px 32px 0 rgb(31 38 135 / 37%); */
  border: 2px solid rgb(128 128 128 / 15%);
  /* backdrop-filter: blur(5px); */
  /* -webkit-backdrop-filter: blur(5px); */
  border-radius: 5px;
  /* border: 1px solid hsla(0, 0%, 100%, 0.18); */
  transition: 0.8s;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  transition: 0.7s;
}
.barangay__services__promo__thumbnail:hover {
  transform: scale(1.03);
  border-image: linear-gradient(
    90deg,
    rgba(54, 128, 247, 1) 0%,
    rgba(240, 173, 71, 1) 100%
  );
  border-image-slice: 1;
  background: #f5f6f9;
}
.barangay__services__promo__thumbnail:first-child {
  margin-right: 20px;
}
.barangay__services__promo__thumbnail:last-child {
  margin-left: 20px;
}
.barangay__services__promo__thumbnail__title {
  position: relative;
  padding-right: 15px;
  margin-right: 25px;
}
.barangay__services__promo__thumbnail__title::after {
  position: absolute;
  content: "";
  width: 1px;
  height: 120%;
  background: rgba(65, 65, 65, 0.3);
  top: -8%;
  right: -4px;
}
.barangay__services__promo__thumbnail__title h2 {
  letter-spacing: 1px;
  font-weight: 400;
}
.barangay__services__promo__thumbnail__title .bsp__days {
  display: inline-block;
  background: linear-gradient(90deg, #3680f7, #f0ad47);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-bottom: 2px;
}
.barangay__services__promo__thumbnail__desc {
  position: relative;
  width: calc(100% - 205px);
}
.barangay__services__promo__thumbnail__desc p {
  font-weight: 400;
  line-height: 1.3;
  font-family: "Poppins", sans-serif !important;
}
/* BARANGAY SERVICES PROMO THUMBNAIL */
/* BARANGAY SERVICES PROMO SECTION */

/* BARANGAY FORMS SECTION */
#barangayFormSection {
  position: relative;
  width: 100%;
  height: auto;
  margin-bottom: 10rem;
  padding: 5rem 0;
  background-color: #f5f6f9;
  /* background: hsla(0, 0%, 100%, 0.2);
  box-shadow: 0 8px 32px 0 rgb(31 38 135 / 37%);
  backdrop-filter: blur(2px);
  -webkit-backdrop-filter: blur(2px); */
}

/* BARANGAY FORMS CONTENT */
.barangay__form_content {
  position: relative;
  max-width: calc(50% - 0px);
  min-width: calc(50% - 0px);
  /* padding: 0 40px; */
}
.barangay__form_content h2 {
  letter-spacing: 1px;
  font-weight: 400;
  padding-bottom: 1rem;
  border-bottom: 2px solid rgb(128 128 128 / 15%);
  margin-bottom: 2rem;
  font-size: 30px;
}
.barangay__form_content h2 span {
  letter-spacing: 1px;
  font-weight: 400;
  display: inline-block;
  background: linear-gradient(90deg, #3680f7, #f0ad47);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
/* BARANGAY FORMS CONTENT */

/* BARANGAY FORMS CONTAINER */
.barangay__form__container {
  position: relative;
  /* max-width: calc(60% - 60px);
  min-width: calc(60% - 60px);
  padding: 0 40px 0 20px; */
}
.barangay__form__container form {
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}

.input__size__frame__barangay {
  position: relative;
  max-width: calc(50% - 10px);
  min-width: calc(50% - 10px);
  height: auto;
  margin: 0 10px;
}
.input__frame__barangay_ml {
  margin-left: 0;
}
.input__frame__barangay_mr {
  margin-right: 0;
}
.input__size__frame__barangay__textarea {
  position: relative;
  max-width: calc(100% - 0px);
  min-width: calc(100% - 0px);
  height: auto;
}
.eula {
  position: relative;
  max-width: calc(80% - 0px);
  min-width: calc(80% - 0px);
  height: auto;
}
.eula p {
  position: relative;
  letter-spacing: 1px;
  font-weight: 400;
  margin-top: 20px;
}
.eula p span a {
  text-decoration: none;
}
.brmis__checkbox {
  position: relative;
  /* max-width: calc(60% - 0px);
  min-width: calc(60% - 0px); */
  height: auto;
  margin-top: 20px;
  margin-left: 8px;
}
.brmis__checkbox input {
  position: absolute;
  top: 0;
  left: 0;
  margin-left: 0;
  transform: scale(1.5);
  border-color: #adadad;
  cursor: pointer;
}
.brmis__checkbox p {
  margin-left: 25px;
  letter-spacing: 1px;
  font-weight: 400;
}
/* BARANGAY FORMS CONTAINER */
/* BARANGAY FORMS SECTION */

/* BARANGAY FEATURES SECTION */
#barangayFeatureSection {
  position: relative;
  width: 100%;
  padding-bottom: 10rem;
}
.barangay__features__container {
  position: relative;
  width: 100%;
  height: auto;
}
/* BARANGAY FEATURES CONTENT */
.barangay__features__content {
  position: relative;
  max-width: calc(100% - 0px);
  min-width: calc(100% - 0px);
  padding: 0;
  text-align: center;
}
.barangay__features__content h3 {
  letter-spacing: 1px;
  font-weight: 400;
}
.barangay__features__content h2 {
  letter-spacing: 1px;
  font-weight: 400;
  margin-bottom: 20px;
}
.barangay__features__content h2 span {
  letter-spacing: 1px;
  font-weight: 400;
  display: inline-block;
  background: linear-gradient(90deg, #3680f7, #f0ad47);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.barangay__features__content p {
  letter-spacing: 1px;
  font-weight: 400;
  line-height: 1.2;
}
.barangay__features__hero__smallview {
  display: none;
}
/* BARANGAY FEATURES CONTENT */

/* BARANGAY FEATURES HERO */
.barangay__features__hero {
  position: relative;
  max-width: calc(100% - 0px);
  min-width: calc(100% - 0px);
  padding: 0;
  margin-top: 100px;
}
.barangay__feature__hero__container {
  position: relative;
  width: 100%;
}
.brims__first__bg {
  background: rgba(88, 169, 250, 0.2);
  width: 575px;
  height: 575px;
  border-radius: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}
.brims__second__bg {
  background: rgba(88, 169, 250, 0.3);
  width: 400px;
  height: 400px;
  border-radius: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.brims__third__bg {
  background: rgba(88, 169, 250, 0.5);
  width: 275px;
  height: 275px;
  border-radius: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.brims__hero {
  position: relative;
  width: 250px;
  height: auto;
}
