/* bpm */
.bpm__container {
  position: absolute;
  top: 0px;
  left: 50%;
  transform: translateX(-100%);
  margin-left: -230px;
}
.bpm__container .bpm__title {
  position: relative;
}
.bpm__container .bpm__title p {
  letter-spacing: 1px;
  font-weight: 400;
  display: inline-block;
  background: rgba(255, 255, 255, 0.5);
  padding: 10px 15px;
  border-radius: 10px;
  border: 1px solid transparent;
  z-index: -1;
  transition: 0.7s;
}
.bpm__container:hover .bpm__title p {
  background: hsla(0, 0%, 100%, 0.8);
  box-shadow: 0 8px 32px 0 rgb(31 38 135 / 37%);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  border: 1px solid #3680f7;
}
.bpm__container:hover .bpm__title p span {
  font-weight: 700;
  display: inline-block;
  background: linear-gradient(90deg, #3680f7, #f0ad47);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.bpm__container .bpm__title .bpm__line__border {
  position: relative;
  display: inline-block;
}
.bpm__container .bpm__title .bpm__line__border .bpm__line {
  position: absolute;
  width: 100px;
  height: 3px;
  background: rgba(255, 255, 255, 0.9);
  top: -7px;
  left: 0;
  transition: 0.7s;
}
.bpm__container:hover .bpm__title .bpm__line__border .bpm__line {
  background: rgba(54, 128, 247, 0.6);
  box-shadow: 0 8px 32px 0 rgb(31 38 135 / 37%);
}
.bpm__container .bpm__title .bpm__line__border .bpm__line::after {
  position: absolute;
  content: "";
  width: 100px;
  height: 3px;
  background: rgba(255, 255, 255, 0.9);
  left: 85px;
  top: 35px;
  transform: rotate(45deg);
  transition: 0.7s;
}
.bpm__container:hover .bpm__title .bpm__line__border .bpm__line::after {
  background: rgba(54, 128, 247, 0.6);
  box-shadow: 0 8px 32px 0 rgb(31 38 135 / 37%);
}
.bpm__container .bpm__title .bpm__line__border .bpm__line .bpm__circle {
  position: absolute;
  width: 12px;
  height: 12px;
  background: rgba(255, 255, 255, 0.9);
  border-radius: 100%;
  top: 70px;
  right: -80px;
  z-index: 4;
  transition: 0.7s;
}
.bpm__container:hover .bpm__title .bpm__line__border .bpm__line .bpm__circle {
  background: rgba(54, 128, 247, 0.6);
}
.bpm__container:hover
  .bpm__title
  .bpm__line__border
  .bpm__line
  .bpm__circle__blink {
  position: absolute;
  width: 24px;
  height: 24px;
  background: rgba(255, 255, 255, 1);
  border-radius: 100%;
  top: 64px;
  right: -86px;
  z-index: 4;
  -webkit-animation: pulse 1.5s ease-out;
  -moz-animation: pulse 1.5s ease-out;
  animation: pulse 1.5s ease-out;
  -webkit-animation-iteration-count: infinite;
  -moz-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
}
.bpm__container:hover .bpm__title .bpm__line__border .bpm__line .bpm__circle {
  background: rgba(54, 128, 247, 0.6);
}
.bpm__container:hover
  .bpm__title
  .bpm__line__border
  .bpm__line
  .bpm__circle__blink {
  position: absolute;
  width: 24px;
  height: 24px;
  background: rgba(255, 255, 255, 1);
  border-radius: 100%;
  top: 64px;
  right: -86px;
  z-index: 4;
  -webkit-animation: pulse 1.5s ease-out;
  -moz-animation: pulse 1.5s ease-out;
  animation: pulse 1.5s ease-out;
  -webkit-animation-iteration-count: infinite;
  -moz-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
}
.bpm__container .bpm__title .bpm__line__border .bpm__line .bpm__icon {
  position: absolute;
  top: 10px;
  right: -67px;
}
.bpm__container .bpm__title .bpm__line__border .bpm__line .bpm__icon img {
  position: relative;
  width: 55px;
  height: auto;
  z-index: 9;
  transition: 0.7s;
}
.bpm__container:hover .bpm__title .bpm__line__border .bpm__line .bpm__icon img {
  transform: scale(1.2);
}
