/* GOOGLE TOOLS */

#educationGoogleTools {
  position: relative;
  width: 100%;
  height: auto;
  padding-bottom: 10rem;
}

.google-tools-container {
  position: relative;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
}

/* Google Tools Accordion */

.google-tools-accordion {
  box-shadow: none !important;
  border-bottom: 2px solid rgb(128 128 128 / 15%);
}

.google-tools-accordion::before {
  background-color: transparent !important;
}

.google-accordion-heading {
  font-family: "Poppins", sans-serif !important;
  font-weight: 400 !important;
  font-size: 20px !important;
  color: rgb(0, 0, 0);
  transition: all 1s ease-out;
}

.google-tools-accordion:hover .google-accordion-heading {
  background: linear-gradient(
    90deg,
    rgba(54, 128, 247, 1) 0%,
    rgba(240, 173, 71, 1) 100%
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-left: 5px;
}

.expand-icon {
  color: rgba(240, 173, 71, 1);
  border: 2px solid #3680f7;
  border-radius: 50%;
  font-size: 20px !important;
  padding: 2px;
}

.accordion-description ul {
  margin: 0;
  padding: 0;
}

.accordion-des-ul {
  padding-left: 2rem !important;
}

.accordion-description ul li {
  font-family: "Poppins", sans-serif !important;
  font-weight: 400 !important;
  font-size: 16px !important;
  color: rgb(0, 0, 0);
  padding-left: 0.3rem;
}

